import React from 'react';
import { render } from 'react-dom';
import Note from './Note.jsx';
import PreviewLink from './PreviewLink.jsx';

import './style.sass';

function deserialize(plugin) {
  const fieldValue = plugin.getFieldValue(plugin.fieldPath);

  if (!fieldValue) {
    if (plugin.parameters.instance.previewLinks) {
      return [
        {
          url : "test",
          timestamp: (new Date()).toISOString(),
        },
      ];
    }

    return [];
  }

  return JSON.parse(fieldValue);
}

function serialize(previewLinks) {
  return JSON.stringify(previewLinks);
}

window.DatoCmsPlugin.init().then((plugin) => {
  plugin.startAutoResizer();

  class App extends React.Component {
    constructor(props) {
      super(props);
      console.log("plugin.parameters",plugin.parameters)
      console.log("plugin",plugin)
      console.log("locale",plugin.locale)
      console.log("fields",plugin.fields)
      console.log("plugin.site.attributes.locales",plugin.site.attributes.locales)
      this.state = {
        previewLinks: deserialize(plugin),
      };
    }

    componentDidMount() {
      this.unsubscribe = plugin.addFieldChangeListener(plugin.fieldPath, () => {
        this.setState({ previewLinks: deserialize(plugin) });
      });
    }

    componentWillUnmount() {
      this.unsubscribe();
    }

    handleAddNote(e) {
      const { previewLinks } = this.state;
      console.log("plugin.locales",plugin.locale)
      console.log("plugin.locales",plugin.locale)
      console.log("plugin.field.attributes.localized;",plugin.field.attributes)

      e.preventDefault();

      plugin.setFieldValue(
        plugin.fieldPath,
        serialize(previewLinks.concat({
          url: plugin.locale || "null",
          timestamp: (new Date()).toISOString(),
        })),
      );
    }

    handleEdit(index, url) {
      const { previewLinks } = this.state;
      console.log("plugin.site.attributes.locales",plugin.site.attributes.locales)
      console.log("plugin.locales",plugin)

      plugin.setFieldValue(
        plugin.fieldPath,
        serialize(Object.assign(
          [], previewLinks,
          {
            [index]: Object.assign({}, previewLinks[index], { url }),
          },
        )),
      );
    }

    handleDelete(index) {
      const { previewLinks } = this.state;

      plugin.setFieldValue(
        plugin.fieldPath,
        serialize(previewLinks.filter((note, i) => i !== index)),
      );
    }

    renderPreviewLink(previewLink, i) {
      return (
        <PreviewLink
          key={previewLink.timestamp}
          previewLink={previewLink}
          onEdit={this.handleEdit.bind(this, i)}
          onDelete={this.handleDelete.bind(this, i)}
        />
      );
    }

    render() {
      const { previewLinks } = this.state;
      console.log("previewLinks",previewLinks)
      return (
        <div className="previewLinks">
          {previewLinks.map(this.renderPreviewLink.bind(this))}
          <button
            type="button"
            className="previewLinks__add"
            onClick={this.handleAddNote.bind(this)}
          >
            Add a new note...
          </button>
        </div>
      );
    }
  }

  render(<App />, document.body);
});
